.profile__name {
  max-width: 336px;
  font-style: normal;
  font-weight: 500;
  font-size: 42px;
  line-height: 48px;
  padding-top: 7px;
  margin: 0;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

@media screen and (max-width: 870px) {
  .profile__name {
    font-size: 27px;
    line-height: 33px;
  }
}
