.popup__title {
  font-family: "Inter", "Arial", sans-serif;
  font-style: normal;
  font-weight: 900;
  font-size: 24px;
  line-height: 29px;
  margin: 0;
  padding: 34px 0 54px 36px;
  color: var(--black);
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

@media screen and (max-width: 560px) {
  .popup__title {
    font-size: 18px;
    line-height: 22px;
    padding: 25px 22px 75px 22px;
  }
}
